import axios from 'axios';
import Qs from 'qs';
import code from './code.js'

axios.defaults.headers['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8'
axios.defaults.transformRequest = [obj => Qs.stringify(obj)]

// let test='http://www.wakal.club'
// let test='http://104.168.218.137:2333'
// let test='https://104.168.218.137:9000'
let test='https://www.wakal.club'
// let test='https://localhost:9000'
// let production='https://www.wakal.club'
let urlroot=test
// let urlroot=production
const doajax = (url, data, type) => {
  var overurl = urlroot +'/rest/api'+ url
  // var token=sessionStorage.getItem('token')?sessionStorage.getItem('token'):false
  // if(url!='/login/login'){
  //   data.token=token;
  // }
  let config;
  if (type === 'get') {
      config = {
          method: type,
          params: data,
      }
    return axios(overurl, config);
  } else if(type=='post') {
    // debugger
      data=code.addcode(JSON.stringify(data),'asyncawait4343')
      return axios.post(overurl,{data})
  }
}

const ajaxget = (url, data, suc) => {
  doajax (url, data, 'get').then((res,s,n) => {
      // let {
      //     status,
      //     msg
      // } = res.data;
      // res=JSON.parse(code.pullcode((res.data),'key1'))
      suc(res);
  });
}
const ajaxpost = (url, data, suc) => {
  doajax (url, data, 'post').then((res) => {
    
    suc(res);
  });
}

export default {
  ajaxpost,
  ajaxget,
  urlroot
}



