// import Vue from 'vue';
// import Router from 'vue-router';
// Vue.use(Router);
// import Index from '@/pages/home';
// import count from '@/pages/count';
// import Prict from '@/pages/prict';
// import Login from '@/pages/login/login';
// import globla from '@/pages/globla/3dRound';
// import loading from '@/components/loading.vue';

const Index = ()=>import("@/pages/home")
const count = ()=>import("@/pages/count")
const Prict = ()=>import("@/pages/prict")
const Login = ()=>import("@/pages/login/login")
const globla = ()=>import("@/pages/globla/3dRound")
const loading = ()=>import("@/components/loading.vue")
const three = ()=>import("@/pages/three")
const pointerLockControls = ()=>import("@/pages/three/pointerLockControls")
const curve = ()=>import("@/pages/three/curve")
const PerspectiveCapture = ()=>import("@/pages/three/PerspectiveCapture")


const router = new VueRouter({
    routes: [{
            path: '/',
            name: 'index',
            component: Index
        },
        {
            path: '/count',
            name: 'count',
            component: count
        },
        {
            path: '/prict',
            name: 'prict',
            component: Prict
        },
        {
            path: '/login',
            name: 'login',
            component: Login
        },
        {
            path: '/loading',
            name: 'loading',
            component: loading
        },
        {
            path: '/globla',
            name: 'globla',
            component: globla
        },
        {
            path: '/three',
            name: 'three',
            component: three
        },
        {
            path: '/three/pointerLockControls',
            name: 'pointerLockControls',
            component: pointerLockControls
        },
        {
            path: '/three/curve',
            name: 'curve',
            component: curve
        },
        {
            path: '/three/PerspectiveCapture',
            name: 'PerspectiveCapture',
            component: PerspectiveCapture
        },
        {
            path: '*',
            redirect: "/"
        },
    ]
});
export default router;



