// 请求前拦截
axios.interceptors.request.use(function(config) {
    // Loading.service({
    //     lock: true,
    //     text: 'Loading',
    //     spinner: 'el-icon-loading',
    //     background: 'rgba(0, 0, 0, 0.7)'
    // })
    return config
})
// 请求后拦截
axios.interceptors.response.use(function(res) {
    // Loading.service().close()
    res=JSON.parse(code.pullcode(res.data,'asyncawait4343'))
    // if(res.code==0){return false}
    Message({ message: res.msg, duration: 1500,iconClass:'none' })
    return res
}, function(err) {
    console.log(err)
        // console.log(err.response.status)
    Loading.service().close()
    Message({ message: '数据接收失败', duration: 1500,iconClass:'none' })
    return err
})
//过滤器
Vue.filter('toUSD', function(value) {　 return value ? value.toFixed(0) : value })
Vue.filter('toUSD2', function(value) {　 return value ? value.toFixed(2) : value })
Vue.filter('date', function(date) {　
    date=new Date(date)
    var y = date.getFullYear();
    var M = date.getMonth() + 1;
    var d = date.getDate();
    var h = date.getHours();
    var min = date.getMinutes();
    var s = date.getSeconds();
    var hd = `${y}-${M}-${d} ${h}:${min}:${s}`
    return hd
})
//获取屏幕宽高
Vue.prototype.getViewportSize = {
      width: window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth,
      height: window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight
    };
//实例化
let vue = new Vue({
    el: '#app',
    router,
    store,
    render: h => h(App)
});



export default vue


