
import https from './https';

// 登录
let login=(data,suc)=>{ var url='/login'; https.ajaxpost(url,data,res=>{ return suc(res) })}
//登出
let logout=(data,suc)=>{ var url='/index/logout'; https.ajaxpost(url,data,res=>{ return suc(res)})}
//首页
let user=(data,suc)=>{ var url='/index'; https.ajaxget(url,data,res=>{ return suc(res)})}
//用户查询
let chart=(data,suc)=>{ var url='/chart'; https.ajaxget(url,data,res=>{ return suc(res)})}
//建议提交
let addproposal=(data,suc)=>{ var url='/add/proposal';https.ajaxpost(url,data,res=>{return suc(res)}) }
//查询建议
let haveproposal=(data,suc)=>{ var url='/have/proposal';https.ajaxget(url,data,res=>{return suc(res)}) }
//签到
let signIn=(data,suc)=>{ var url='/user/SignIn';https.ajaxpost(url,data,res=>{return suc(res)}) }
//查询访问数
let getcount=(data,suc)=>{ var url='/getcount';https.ajaxpost(url,data,res=>{return suc(res)}) }
export default {
  login,logout,user,chart,addproposal,haveproposal,signIn,getcount
}



