<template>
  <div id="app" class="fsb ais" style="">
    <div class='bg'></div>
    <div class='bg_'></div>
    <load v-show='this.$store.state.loading'></load>
    <side class="fixed z-index-99"></side>
    <div class="rou fg2 rel">
        <router-view class="" style=""></router-view>
    </div>
    <div class="sockBox txal">
        <nametran class=""></nametran>
        <socket class=""></socket>
    </div>
  </div>
</template>

<script>
    const load = ()=>import('@/components/loading.vue')
    const side = ()=>import('@/components/side.vue')
    const socket = ()=>import('@/components/socketio.vue')
    const nametran = ()=>import('@/components/nametran.vue')
    // const VueSocketIO = ()=>import('vue-socket.io')
    // import VueSocketIO from 'vue-socket.io'
    // import http from './common/https.js'
    // const http = ()=>import('./common/https.js')
	import * as THREE from 'Three';
    export default {
        components: {
            load,
            side,
            socket,
            nametran,
        },
        data() {
            return {
                path: '',
            }
        },
        created(){
            //设置全局守卫
            this.$router.beforeEach((to,from,next)=>{
                let islogin=this.$store.state.isLogin
                this.$store.commit('updateLoading',true)
                let that=this
                let timer=setTimeout(function(){
                    that.$store.commit('updateLoading',false)
                },800)
                next()
                // if(!islogin){
                //    next('/login')
                // }else{
                //     next()
                // }
                    
            })
            
        },
        // 判断路由
        updated() {
            
        },
        mounted() {
            this.path = this.$route.path;
        },
        watch: {
            $route(to, from) {
                this.path = to.path
                if (this.path !== '/login') {
                    document.documentElement.scrollTop = 0
                }
            },
            // 'token':function (n,o) {
            //   console.log(n)
            // }
        }
    }
</script>

<style>
    
    #app {font-family:happy;}
    .el-pagination.is-background .el-pager li:not(.disabled).active{
        background-image: linear-gradient(to right, #ff0000, #000000);
    }
    .el-select-dropdown__item.selected{
        color:#ea081b !important;
        font-weight: 700 !important;
    }
    .el-pagination.is-background .el-pager li:not(.disabled):hover{
        color:#ea081b !important;
    }
    .Mybtn{
        color: aliceblue;
        font-family: happy;
        /* animation: btnfram 5s cubic-bezier(1,1,1,1) 0s infinite; */
        background-image: linear-gradient(to right, #ff0000, #000000);
        border: none;
        border-radius: 5px;
        padding: 3px 14px;
    }
    .Mybtn.active.focus {
        outline: none;
        border-color: transparent;
        box-shadow:none;
    }

    .nametranBox{
        position: fixed;
        right: 20px;
        top:20px;
    }
    .sockBox{
        position: fixed;
        right: 20px;
        top:20px;
    }
    .el-message{
        background-image: linear-gradient(to right, #000000, #ff0000) !important;
        border:none !important;
        color:white !important;
        font-family:happy !important;
    }

    .rou {
        margin-left: 200px;
        margin-right: 370px;
    }
    
    .bg {
        background-image: linear-gradient(to right, #ff0000, #000000);
        height: 100vh;
        width: 100vw;
        position: fixed;
        z-index: -3;
    }
    
    .bg_ {
        background-image: linear-gradient(to right, #000000, #ff0000);
        height: 100vh;
        width: 100vw;
        position: fixed;
        z-index: -2;
    }
    
    @keyframes myfirst {
        0% {
            opacity: 1;
        }
        50% {
            opacity: 0;
        }
        100% {
            opacity: 1;
        }
    }
    @keyframes btnfram {
        0% {
            background-image: linear-gradient(to right, #ff0000, #000000);
        }
        50% {
            background-image: linear-gradient(to right,#000000, #ff0000);
        }
        100% {
            background-image: linear-gradient(to right, #ff0000, #000000);
        }
    }

    .classname{
        animation: move 1s ease 0 1;
    }
    @keyframes move {
        0% {
            opacity: 1;
        }
        50% {
            opacity: 0;
        }
        100% {
            opacity: 1;
        }
    }
</style>


