import createPersistedState from "vuex-persistedstate"
import Export from './module/export';

//存数据
const state={
  title:'',//默认初始值
  isLogin:false,
  user:{},
  loading:false,
  istoday:true
}

// 取数据
const getters={
  title:state=>state.title,
  isLogin:state=>state.isLogin,
  user:state=>state.user,
  loading:state=>state.loading,
  istoday:state=>state.istoday,
}

// 改数据
const mutations={
  updateTitle:(state,payload)=>{
    state.title=payload;
  },
  updateLogin:(state,payload)=>{
    state.isLogin=payload;
  },
  updateUser:(state,payload)=>{
    state.user=payload;
  },
  updateLoading:(state,payload)=>{
    state.loading=payload;
  },
  updateIstoday:(state,payload)=>{
    state.istoday=payload;
  },
}

// 提交
const actions={
  updateTitle:({commit},payload)=>{
    commit('updateTitle',payload);
  },
  updateLogin:({commit},payload)=>{
    commit('updateLogin',payload)
  },
  updateUser:({commit},payload)=>{
    commit('updateUser',payload)
  },
  updateLoading:({commit},payload)=>{
    commit('updateLoading',payload)
  },
  updateIstoday:({commit},payload)=>{
    commit('updateIstoday',payload)
  },
}

const store=new Vuex.Store({
  plugins: [createPersistedState({
    storage: window.sessionStorage,
  })],
  state,
  getters,
  mutations,
  actions,
  modules:{
    Export
  }
})

export default store;



